import Error from '.Error';
import Set from '.Set';
import RegExp from '.RegExp';
import keys from '.Object.keys';
import process from '.process';
import NaN from '.NaN';
import Null from '.null';

import { newRegExp } from '@ltd/j-regexp';

import gen from './dependencies.gen';

export const forAliasRE = /\s(?:in|of)\s/s;
export const slotRE = /^(?:#|v-slot$)/;// /^(?:#|v-slot(?::|$))/;
export const emptySlotScopeToken = '_empty_';
export const SLOT_DIRECTIVE = /^v-(?:once|for|if|else(?:-if)?|bind)$/;
export const BAD_SLOT_NAME = /^[$_]/;// Vue2: $hasNormal $key $stable _normalized __proto__ // Vue3: $stable _*
export const BAD_V_SLOT_NAME = /^#[$_]/;// /^(?:#|v-slot:)[$_]/;
export const BAD_SCOPE = '__proto__';
export const BAD_KEY = '__proto__';
export const BAD_REF = '__proto__';
export const BAD_INS = /\r(?!\n)|[\u2028\u2029]/;
const NON_ASCII = '\\u3007\\u4E00-\\u9FA5';
export const NON_ASCII_SIMPLE_PATH = newRegExp`
	^\s*
		(?:
			[A-Za-z_$]
			[\w$]*
		)?
		[${NON_ASCII}]
		[\w$${NON_ASCII}]*
	\s*$
`;
export const BUILT_IN = new Set(`
	keep-alive
	slot
	suspense
	teleport
	template
	transition
	transition-group
`.match(/\S+/g) );
export const STYLE_BY_COMPONENT_IS   
	                                                                                                                             
	                                                                                                                 
	                                                                      
         = null;

export const { parse, acornInjectPlugins, rollup, minify }                                  = require('@ltd/j-es-plus');
export const { simple }                              = require('acorn-walk');
export const findGlobals                                      = require('@ltd/acorn-globals');
export const transpileModule                             = require('@ltd/j-ts');

export const { 3: compile3, 2: compile2 }   
	                                                                                                    
	                                                                                                        
  = ( () => {
	
	const Const3dom = Replacer(
		[ `|| node.tag === 'style'` ],
		[ `makeMap('style,iframe,script,noscript', true)`, `makeMap('style,script', true)` ],
		[ /compilerCore\.isBuiltInType\(tag, ([^)]+)\)/g, (match        , p1        ) => `tag===${p1.replace(/\B[A-Z]/g, (W        ) => `-${W.toLowerCase()}`).toLowerCase()}`, 2 ],
	);
	const Const3core = Replacer(
		[ /!shared\.isGloballyWhitelisted\([^)]*\)|identifier\.name !== `(?:require|arguments)`/g, 'true', 4 ],
		[ /isBuiltInType\(tag, ([^)]+)\)/g, (match        , p1        ) => `tag===${p1.replace(/\B[A-Z]/g, (W        ) => `-${W.toLowerCase()}`).toLowerCase()}`, 4 ],
	);
	const Let3core = Replacer(
		[ /push\(`const /g, 'push\(`let ', NaN ],
	);
	
	const Var2 = Replacer(
		[ /(?<! in ){}(?=[);,])(?!\)\.)/g, `Object.create(null)`, 23 ],
		[ `el.attrsMap.hasOwnProperty('v-for')`, `hasOwn(el.attrsMap, 'v-for')` ],
		[ `el.tag === 'style' ||` ],
		[ /(var simplePathRE = \/)(.*?\*)/s, (match        , pre        , aim        ) => pre + aim.replace(/(?<=\$)/g, NON_ASCII) + '$|' + aim ],
		[ RegExp(`function gen(${keys(gen[2]).join('|')}) \\((.*?)\\n}\\n`, 'gs'), (func        , name        ) => gen[2][name                       ].var, 2 ],
		[ /undefined(?='\) \+|'\r?\n|")|(?<=')\$\$v(?=')/g, (origin        ) => ( { undefined: 'void null', $$v: '$event' }[origin                       ] ), 4 ],
	);
	const Const2 = Replacer(
		[ RegExp(`function gen(${keys(gen[2]).join('|')}) \\((.*?)\\n}\\n`, 'gs'), (func        , name        ) => gen[2][name                       ].const, 2 ],
		[ /function\((|\$event|" \+ alias \+ iterator1 \+ iterator2 \+ "|" \+ slotScope \+ ")\){/g, (match        , p1        )         => `(${p1})=>{`, 7 ],
	);
	const Let2 = Replacer(
		[ /const /g, 'let ', NaN ],
	);
	
	const _prod         = process.env.NODE_ENV==='production' ? '.prod' : '';
	
	const filename3core         = require.resolve(`@vue/compiler-dom/node_modules/@vue/compiler-core/dist/compiler-core.cjs${_prod}.js`);
	const filename3dom         = require.resolve(`@vue/compiler-dom/dist/compiler-dom.cjs${_prod}.js`);
	let content3core         = require('fs').readFileSync(filename3core, 'utf8');
	let content3dom         = require('fs').readFileSync(filename3dom, 'utf8');
	const Compile3 = (content_dom        , content_core        ) => {
		return Exports                                    (content_dom, filename3dom, Null({
			'@vue/compiler-core': Exports                                     (content_core, filename3core),
		})).compile;
	};
	
	const filename2         = require.resolve('vue-template-compiler/build.js');
	let content2         = require('fs').readFileSync(filename2, 'utf8');
	const Compile2 = (content        ) => Exports                                        (content, filename2).compile;
	
	return {
		3: {
			var ()        {
				throw Error(`Vue 3 无法运行在 ES 5 中，请使用 const 或 let 模式`);
			},
			const: Compile3(
				content3dom = Const3dom(content3dom),
				content3core = Const3core(content3core),
			),
			let: Compile3(
				content3dom,
				Let3core(content3core),
			),
		},
		2: {
			var: Compile2(
				content2 = Var2(content2),
			),
			const: Compile2(
				content2 = Const2(content2),
			),
			let: Compile2(
				Let2(content2),
			),
		},
	};
	
	function Replacer (...replacers   
		                                                                         
		                                                                                           
	) {
		return function replacer (            content        ) {
			for ( let [ search, replacer = '', count = 1 ] of replacers ) {
				if ( search instanceof RegExp && search.global===( count===1 ) ) { throw Error(`jVue 内部错误`); }
				content = content.replace(search, (...args          ) => {
					--count;
					return typeof replacer==='function' ? replacer(...args) : replacer;
				});
				if ( count ) { throw Error(`jVue 内部版本依赖错误`); }
			}
			return content;
		};
	}
	
	function Exports    (content        , filename        , cache            ) {
		const module_require = ( require('module').createRequire ?? require('module').createRequireFromPath )(filename);
		const module = Null({ exports: {}      });
		require('vm').compileFunction(
			content,
			[ 'exports', 'require', 'module', '__filename', '__dirname' ],
			{ filename }
		).call(module.exports, module.exports, cache ? (id        ) => cache[id] ?? module_require(id) : module_require, module, filename, require('path').dirname(filename));
		return module.exports;
	}
	
} )();
